<template>
  <div class="ElectronicsBox">
    <p
      class="Ele"
      :style="{
        color: numColor,
        background: bgColor,
        fontSize: fontSize + 'px',
      }"
    >
      {{ displayNumber }}
    </p>
  </div>
</template>
<script>
import { ref, onMounted, onUnmounted } from "vue";
export default {
  name: "h-numAnimation",
  // :eleNumber     
  // :fontSize      
  // :numColor      
  // :bgColor            none
  // :numSpeed                100
  // :numFrequency              20
  props: {
    eleNumber: {
      type: Number,
      default: 99,
    },
    bgColor: {
      type: String,
      default: "#fff",
    },
    numColor: {
      type: String,
      default: "#000",
    },
    fontSize: {
      type: Number,
      default: 16,
    },
    numSpeed: {
      type: Number,
      default: 100,
    },
    numFrequency: {
      type: Number,
      default: 20,
    },
  },
  setup(props) {
    const MyEleNumber = ref(props.eleNumber); //
    const displayNumber = ref(0); //
    let numTime = null; //
    onMounted(() => {
      animationNum();
    });
    // 
    onUnmounted(() => {
      window.clearInterval(numTime);
      numTime = null;
    });
    // 
    const animationNum = function () {
      // 1
      let pace = 1;
      //numFrequency11
      if (MyEleNumber.value > props.numFrequency) {
        //20     100 --> 5
        pace = parseInt(MyEleNumber.value / props.numFrequency); //
      }
      numTime = setInterval(() => {
        displayNumber.value += pace; //
        if (displayNumber.value >= MyEleNumber.value) {
          //
          clearInterval(numTime);
          numTime = null; //
          displayNumber.value = MyEleNumber.value; //
        }
      }, props.numSpeed);
    };
    return {
      displayNumber,
    };
  },
};
</script>
<style lang="less" scoped>
/* main.js */
.Ele {
  text-align: center;
  font-family: ele;
  /*  */
  letter-spacing: 7px;
  margin: 10px 0;
  /* font-size: 60px; */
  /* color */
  /* color: #00ffff; */
  /* -webkit-text-fill-color: #00ffff; */
  /*  */
  /* -webkit-text-stroke: 1px #000000;   */
}
.ElectronicsBox {
  width: 100%;
  height: 100%;
}
</style>
