<template>
  <div class="scrollbar-box" :class="{ hide :  winWidth < 500 }" :style="{ height: height }">
    <div class="scrollbar-y">
      <div ref="scrollRef" class="scroll-wrap" @scroll="onMosewheel">
        <slot></slot>
      </div>
      <div
        ref="barRef"
        class="scrollbar-track"
        :style="{
          backgroundColor: heightPre == 1 ? 'rgba(0,0,0,0)' : slotColor,
        }"
      >
        <!-- 
        v-show
        backgroundColor: heightPre == 1 ? 'rgba(0,0,0,0)' : scrollColor,
       -->
        <div
          :style="{
            height: barHeight + 'px',
            width: scrollWidth + 'px',
            transform: 'translateY(' + translateY + 'px)',
            backgroundColor: heightPre == 1 ? 'rgba(0,0,0,0)' : scrollColor,
          }"
          class="scrollbar-thumb"
          @mousedown.stop.prevent="moveStart"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  defineComponent,
  toRefs,
  onMounted,
  nextTick,
  reactive,
  onUnmounted,
} from "vue";
export default defineComponent({
  name: "h-scrollContainer",
  props: {
    height: {
      //:height="'200px'"
      type: String,
      default: "100%",
    },
    slotColor: {
      // :slotColor="'red'"
      type: String,
      default: "rgba(0,0,0,0)",
    },
    scrollColor: {
      //  :scrollColor="'red'"
      type: String,
      default: "#999",
    },
    scrollWidth: {
      // :scrollWidth="7"
      type: Number,
      default: 12,
    },
  },
  setup(props, ctx) {
    const data = reactive({
      scrollRef: null, // 
      barRef: null, // 
      translateY: 0, // 
      heightPre: 0, // 
      barHeight: 0, // 
      winWidth : document.body.clientWidth //
    });
    let time = null; // 
    let isMove = false; // 
    let moveClientY = 0; // 
    let trackHeight = 0; // 
    let wrapHeight = 0; // 
    let wrapContentHeight = 0; // 
    

    // 
    onMounted(() => {
      monitorWindow(); //
      monitorScrollBar(); //
      nextTick(() => {
        //dom
        calculationLength(); //
      });
    });
    // 
    onUnmounted(() => {
      window.clearInterval(time);
      time = null;
    });
    // 
    const monitorWindow = function () {
      let time; //
      window.addEventListener("resize", () => {
        data. winWidth = document.body.clientWidth;  //
        clearTimeout(time);
        time = setTimeout(() => {
           //500
          
          
          // console.log("");
          initScrollListner();
        }, 500);
      });
    };
    //
    const monitorScrollBar = function () {
      var monitorUl = data.scrollRef.children[0];
      // var monitorDiv= document;    // document
      let MutationObserver =
        window.MutationObserver ||
        window.WebKitMutationObserver ||
        window.MozMutationObserver;
      let observer = new MutationObserver(function (mutations) {
        // console.log("");
        initScrollListner();
      });

      // childList  
      // 
      observer.observe(monitorUl, {
        attributes: true,
        childList: true,
        // subtree: true,    //
      });
      // document
      // observer.observe(monitorDiv, {
      //   childList:	true,
      //   subtree:	true	,
      // });
    };

    // 
    const calculationLength = function () {
      // console.log("");
      // initScrollListner
      // dom
      // 10
      time = setInterval(() => {
        // 
        initScrollListner();
      }, 50);
      // 500
      setTimeout(() => {
        window.clearInterval(time);
        time = null;
      }, 2000);
    };
    // 
    const initScrollListner = function () {
      let scroll = data.scrollRef;
      let bar = data.barRef;
      // scroll
      if (scroll) {
        wrapContentHeight = scroll.scrollHeight;
        wrapHeight = scroll.clientHeight;
        trackHeight = bar.clientHeight;
        // console.log(wrapContentHeight ,wrapHeight);
        //  /    100 150
        data.heightPre = wrapHeight / wrapContentHeight;
        //        
        data.barHeight = data.heightPre * trackHeight;
      }
    };
    // 
    const onMosewheel = function (e) {
      // scrollTop
      // offsetHeight
      // scrollHeight
      // data.translateY
      data.translateY = e.target.scrollTop * data.heightPre;
      if (data.translateY == 0) {
        // 
        arrive("top");
      } else if (
        e.target.scrollTop + e.target.offsetHeight ==
        e.target.scrollHeight
      ) {
        //  +  == 
        arrive("bottom");
      }
    };
    // 
    const arrive = function name(tb) {
      ctx.emit("arrive", tb);
    };
    // 
    const moveStart = function (e) {
      isMove = true;
      // clientYy
      // data.translateY 
      // moveClientY 
      moveClientY = e.clientY - data.translateY;
      moveTo(); //
      moveEnd(); //
    };
    // thumbscrollTop
    const moveTo = function () {
      document.onmousemove = (e) => {
        // 
        if (isMove) {
          // 
          if (e.clientY - moveClientY > trackHeight - data.barHeight) {
            //     translateY
            data.translateY = trackHeight - data.barHeight;
          } else if (e.clientY - moveClientY < 0) {
            //     translateY
            data.translateY = 0;
          } else {
            //
            data.translateY = e.clientY - moveClientY;
          }
          // 
          data.scrollRef.scrollTop = data.translateY / data.heightPre;
        }
      };
    };
    // 
    const moveEnd = function () {
      document.onmouseup = (e) => {
        if (isMove) {
          isMove = false;
        }
      };
    };

    return {
      ...toRefs(data),
      onMosewheel,
      moveStart,
    };
  },
});
</script>
<style lang="less" scoped>
.scrollbar-box {
  height: 100%;
  overflow: hidden;
}
.scrollbar-y {
  position: relative;
  height: 100%;
  margin-right: -17px; //
  .scroll-wrap {
    height: 100%;
    overflow-y: scroll;
  }
  .scrollbar-track {
    position: absolute;
    top: 0;
    right: 17px;
    bottom: 0;
    border-radius: 8px;
    z-index: 20;
    .scrollbar-thumb {
      margin: 0 auto;
      border-radius: 6px;
      cursor: default;
    }
  }
}
// 
//
.hide.scrollbar-box .scrollbar-track {
  display: none;
}
//
.hide.scrollbar-box .scrollbar-y {
  margin: 0;
}
</style>
