          ></span>
          <span class="food-num">{{ foodNum }}</span>
          <span
            class="iconfont icon-qiehuanqiyou next"
            @click="switchFoodNum('next')"
          ></span>
        </div>
      </div>
      <div class="config-box">
        <span class="config-text"></span>
        <div class="config-btn">
          <SwitchBtn
            @getSwitchVal="getSwitchBumpOwn"
            :Disable="!isStop"
            :Switch="isBumpOwn"
          />
        </div>
      </div>
      <div class="config-box">
        <span class="config-text"></span>
        <div class="config-btn">
          <SwitchBtn
            @getSwitchVal="getSwitchBoundary"
            :Disable="!isStop"
            :Switch="isBoundary"
          />
        </div>
      </div>
    </div>
    <!--  -->
    <div class="game-journal">
      <p
        :class="['journal' + index]"
        v-for="(item, index) in bumpText"
        :key="index"
      >
        {{ item }}
      </p>
    </div>
    <!-- logo -->
    <div class="cover-box" v-if="isStop">
      <!-- logo -->
      <img src="~@/assets/img/gluttonous1.png" alt="" />
      <!--  -->
      <p class="start-btn" @click="startGame">
        {{ gameOver ? '' : '' }}
      </p>
      <div class="game-rule-box">
        <p class="rule-text">
          <span></span>
        </p>
        <p class="rule-text">
          <span class="key-btn long"></span>
          <span class="key-btn long"></span>
          
        </p>
        <p class="rule-text">
          <span></span>
          <span class="key-btn">1</span>
          <span class="key-btn">2</span>
          <span class="key-btn">3</span>
          <span class="key-btn">4</span>
        </p>
        <p class="rule-text">
          <span></span>
        </p>
        <p
          class="rule-text"
          v-for="(dir, dirIndex) in directionRule"
          :key="dirIndex"
        >
          <span>{{ overTipsText[gamePlayer[dirIndex]] }}</span>
          <span class="key-btn" v-for="(item, index) in dir" :key="index">{{
            item
          }}</span>
        </p>
        <p class="rule-text">
          <span>OVER</span>
        </p>
        <p class="rule-text">
          <span>1.</span>
        </p>
        <p class="rule-text">
          <span>2.</span>
        </p>
        <p class="rule-text">
          <span>3.</span>
        </p>
      </div>

      <!-- <p class="game-over" v-if="gameOver"></p> -->
      <p class="over-text" :class="overText" v-if="overText">
        {{ overTipsText[overText] + 'OVER' }}
      </p>
    </div>
    <!--  &&  -->
    <div class="gluttonous-range">
      <!--  -->
      <p
        v-for="(food, index) in foodEleArr"
        :key="index"
        class="food-ele"
        :style="{ top: food[0] + 'px', left: food[1] + 'px' }"
      ></p>

      <!--  -->
      <div
        class="gluttonous-body"
        :class="gluttonousBody[index]"
        v-for="(item, index) in gluttonousEle"
        :key="index"
      >
        <p
          class="gluttonous-body-item"
          :class="[currentDir[index], gluttonousBodyItem[index]]"
          :style="{ top: body.top + 'px', left: body.left + 'px' }"
          v-for="(body, bodyIndex) in item"
          :key="bodyIndex"
        ></p>
      </div>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  onBeforeUnmount
} from 'vue';
import { randomNum } from '@/utils/common.js';
export default {
  name: '',
  setup() {
    const data = reactive({
      //
      bumpText: [],
      // 
      directionRule: [
        ['', '', '', ''],
        ['w', 's', 'a', 'd'],
        ['i', 'k', 'j', 'l'],
        ['8', '5', '4', '6']
      ],
      // 
      overTipsText: {
        a: '',
        b: '',
        c: '',
        d: ''
      },
      gamePlayer: ['a', 'b', 'c', 'd'],
      //4
      gluttonousBody: [
        '',
        'gluttonous-body_B',
        'gluttonous-body_C',
        'gluttonous-body_D'
      ],
      //4
      gluttonousBodyItem: [
        '',
        'gluttonous-body-item_B',
        'gluttonous-body-item_C',
        'gluttonous-body-item_D'
      ],
      // 41234
      directionArr: [
        ['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight'],
        ['w', 's', 'a', 'd'],
        ['i', 'k', 'j', 'l'],
        ['8', '5', '4', '6']
      ],

      overText: '', //
      gamePlayerNum: '2', //
      gameOver: false, //
      isStop: true, //
      walkTimes: null,
      foodEleArr: [], //  
      existencePerson: 0, //
      // 
      foodNum: 10, //
      isBoundary: true, //
      isBumpOwn: true, //
      initialLong: 10, //

      // 
      gameFraction: [], //
      speedGrade: [], // 1-10
      currentDir: [], //
      gluttonousEle: [], //
      dirType: [] //    
    });

    // 
    const speedConfig = [];
    onBeforeMount(() => {});
    onMounted(() => {
      watchKeydown(); //
      initial(); //
    });
    // 
    const watchKeydown = () => {
      // 
      window.addEventListener('keydown', (e) => {
        e = e || window.event;
        const btn = e.key;
        // 
        if (['1', '2', '3', '4'].includes(btn) && data.isStop) {
          data.gamePlayerNum = btn;
          initial(); //
        }
        // 
        if (btn === 'Enter' || btn === ' ') {
          1;
          // console.log(data.isStop);
          if (data.isStop) {
            startGame();
          } else {
            //
            stopGames();
          }
        } else {
          // 
          if (data.isStop) return;
          // 
          data.directionArr.forEach((d) => {
            if (d.includes(btn)) {
              // 
              direction(btn);
            }
          });
        }
      });
    };
    // 
    const switchFoodNum = (type) => {
      if (data.isStop) {
        if (type === 'next') {
          if (data.foodNum === 20) return;
          data.foodNum += 1;
        } else {
          if (data.foodNum === 7) return;
          data.foodNum -= 1;
        }
        data.foodEleArr = [];
        initialAllFood();
      }
    };
    // 
    const getSwitchBoundary = (val) => {
      data.isBoundary = val;
    };
    // 
    const getSwitchBumpOwn = (val) => {
      data.isBumpOwn = val;
    };
    // 
    const initial = () => {
      data.existencePerson = parseInt(data.gamePlayerNum);
      data.overText = ''; //
      data.gameOver = false; //
      data.walkTimes = null;
      data.bumpText = [];
      data.foodEleArr = [];
      data.gluttonousEle = [];
      data.speedGrade = [];
      data.gameFraction = [];
      data.dirType = [];
      data.currentDir = [];
      // ,
      for (let n = 0; n < data.gamePlayerNum; n++) {
        let gluttonousList = []; //
        for (let i = 0; i < data.initialLong; i++) {
          gluttonousList.push({ top: 380 + n * 50, left: 700 - i }); //1
        }
        data.gluttonousEle.push(gluttonousList); //
        data.speedGrade.push(1); //
        data.gameFraction.push(0); //
        data.dirType.push(['left', true]); //
        data.currentDir.push(data.directionArr[0][3]); //  ArrowRight
      }
      // 
      let initSpeed = data.speedGrade[0];
      // 1-10 0.1
      for (let i = 0; i < 90; i++) {
        const inSpeed = parseFloat((initSpeed += 0.1).toFixed(1));
        for (let a = 0; a < 10; a++) {
          speedConfig.push(inSpeed);
        }
      }
      initialAllFood(); //
    };
    // 
    const initialAllFood = () => {
      // 
      for (let food = 0; food < data.foodNum; food++) {
        data.foodEleArr.push(foodTL());
      }
    };
    // 
    const startGame = () => {
      // 
      if (data.gameOver) {
        initial(); //
      }
      data.isStop = false;
      // setInterval
      // setTimeout
      data.walkTimes = setInterval(() => {
        walk();
      }, 10);
    };
    // 
    const direction = (type) => {
      let currentDir; //
      let dirType; //
      let gamePlayerIndex; //
      data.directionArr.forEach((item, index) => {
        const dirIndex = item.indexOf(type); //1
        if (dirIndex !== -1) {
          gamePlayerIndex = index; //
          currentDir = data.directionArr[0][dirIndex]; // ArrowUp
        }
      });
      // 
      switch (currentDir) {
        case 'ArrowUp': //
          dirType = ['top', false];
          break;
        case 'ArrowDown': //
          dirType = ['top', true];
          break;
        case 'ArrowLeft': //
          dirType = ['left', false];
          break;
        case 'ArrowRight': //
          dirType = ['left', true];
          break;
      }
      // 
      if (data.dirType[gamePlayerIndex][0] === dirType[0]) return;
      data.currentDir[gamePlayerIndex] = currentDir; //
      data.dirType[gamePlayerIndex] = dirType; //
    };
    // 
    const walk = () => {
      // 
      // 1
      data.gluttonousEle.forEach((strip, stripIndex) => {
        const copyEle = JSON.parse(JSON.stringify(strip));
        if (!strip) return;
        strip.forEach((body, index) => {
          if (index) {
            // 
            strip[index] = copyEle[index - 1];
          } else {
            // index===0  1
            // 
            let headerEle = data.dirType[stripIndex][0];
            //  truefalse
            if (data.dirType[stripIndex][1]) {
              body[headerEle] += data.speedGrade[stripIndex]; //  
            } else {
              body[headerEle] -= data.speedGrade[stripIndex]; //  
            }
            // 
            strip[index] = body;
          }
        });
      });

      eatFood(); // 
    };
    // 
    const eatFood = () => {
      data.gluttonousEle.forEach((strip, stripIndex) => {
        if (!strip) return;
        const inTail = strip[strip.length - 1]; //
        const inheader = strip[0]; //
        // 
        data.foodEleArr.forEach((food, index) => {
          if (
            inheader.top > food[0] - 10 &&
            inheader.top < food[0] + 10 &&
            inheader.left > food[1] - 10 &&
            inheader.left < food[1] + 10
          ) {
            data.gameFraction[stripIndex]++;
            // 
            // 5
            // 
            let addBody = [];
            for (let i = 0; i < 5; i++) {
              addBody.push(inTail);
            }
            strip.push(...addBody);
            // -1110 ,1
            data.speedGrade[stripIndex] = speedConfig[strip.length - 11] || 1;
            generateFood(index); //
          }
        });
      });
      // 
      isGameOver();
    };
    // 
    const isGameOver = () => {
      // 
      data.gluttonousEle.forEach((strip, stripIndex) => {
        if (!strip) return;
        const inheader = strip[0]; //
        if (data.isBumpOwn) {
          strip.forEach((item, index) => {
            // 
            // index == 0 
            if (index) {
              if (inheader.top == item.top && inheader.left == item.left) {
                gameOverFn(data.gamePlayer[stripIndex], '1');
              }
            }
          });
        }

        // 
        if (!data.isBoundary) return;
        // 
        if (
          inheader.top > 760 ||
          inheader.top < 0 ||
          inheader.left > 1850 ||
          inheader.left < 0
        ) {
          gameOverFn(data.gamePlayer[stripIndex], '2');
        }
      });

      // 2
      if (data.existencePerson > 1) {
        // 
        data.gluttonousEle.forEach((strip_bump, bumpIndex) => {
          if (!strip_bump) return;
          const bumpHeader = strip_bump[0];
          // 
          try {
            data.gluttonousEle.forEach((strip_other, otherIndex) => {
              if (!strip_other) return;
              // 
              if (bumpIndex === otherIndex) return;
              // 
              strip_other.forEach((body) => {
                if (
                  bumpHeader.top > body.top - 10 &&
                  bumpHeader.top < body.top + 10 &&
                  bumpHeader.left > body.left - 10 &&
                  bumpHeader.left < body.left + 10
                ) {
                  // data.gamePlayer[otherIndex]
                  gameOverFn(
                    data.gamePlayer[bumpIndex],
                    data.gamePlayer[otherIndex]
                  ); //
                  throw new Error('');
                }
              });
            });
          } catch (e) {
            console.log(e);
            return;
          }
        });
      }
    };
    // 
    const gameOverFn = (type, typeOth) => {
      // over
      let overText = '';
      if (typeOth === '1') {
        // 
        overText = `${data.overTipsText[type]}OVER`;
      } else if (typeOth === '2') {
        // 
        overText = `${data.overTipsText[type]}OVER`;
      } else {
        //
        overText = `${data.overTipsText[type]}${data.overTipsText[typeOth]}${data.overTipsText[type]}OVER`;
      }
      data.bumpText.unshift(overText);
      // over
      if (data.existencePerson - 1) {
        const overIndex = data.gamePlayer.indexOf(type);
        // overnull
        data.gluttonousEle.splice(overIndex, 1, null);
        // 
        data.existencePerson -= 1;
      } else {
        // over
        data.overText = type; //
        data.gameOver = true; //
        stopGames(); //
      }
    };
    // 
    const stopGames = () => {
      data.isStop = true;
      clearInterval(data.walkTimes); //
    };
    // 
    const generateFood = (index) => {
      // 
      data.foodEleArr.splice(index, 1, foodTL());
    };
    // 
    const foodTL = () => {
      return [randomNum(20, 730), randomNum(20, 1820)];
    };
    // 
    onBeforeUnmount(() => {
      stopGames(); //
    });
    return {
      switchFoodNum,
      getSwitchBumpOwn,
      getSwitchBoundary,
      direction,
      startGame,
      stopGames,
      ...toRefs(data)
    };
  }
};
</script>
