<template>
  <table class="Formtable" width="100%">
    <tr
      :style="{
        lineHeight: titLineHeight + 'px',
        fontSize: titFontSize + 'px',
      }"
    >
      <th
        v-for="(tit, index) in titData"
        :key="tit"
        :style="{ width: SetWidth(index) }"
      >
        {{ tit }}
      </th>
    </tr>
    <tr
      :style="{
        lineHeight: conLineHeight + 'px',
        fontSize: conFontSize + 'px',
        color: conColor,
      }"
      v-for="(con, index) in contentData"
      :key="index"
    >
      <td v-for="item in con" :key="item">
        {{ item }}
      </td>
    </tr>
  </table>
</template>

<script>
export default {
  name: "h-formtable",
  props: {
    // 
    conLineHeight: {
      type: Number,
      default: 20,
    },
    // 
    conFontSize: {
      type: Number,
      default: 14,
    },
    // 
    conColor: {
      type: String,
      default: "#777",
    },
    // 
    titLineHeight: {
      type: Number,
      default: 20,
    },
    // 
    titFontSize: {
      type: Number,
      default: 14,
    },
    // 
    colWidth: {
      type: Object,
      default: () => {
        return {
          1: "20%",
          2: "40%",
          4: "20%",
        };
      },
    },
    // 
    titData: {
      type: Array,
      default: () => {
        return ["", "", "", "", ""];
      },
    },
    // 
    contentData: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  setup(props, ctx) {
    // 
    const SetWidth = function (index) {
      // index0 +1
      return props.colWidth[index + 1];
    };
    //,
    const paramsError = function () {
      const cData = props.contentData;
      const tData = props.titData;
      if (cData.length == 0) {
        console.warn("Formtable ");
        ctx.emit("errorEvent", "nullData");
      } else {
        for (let i = 0; i < cData.length; i++) {
          if (cData[i].length !== tData.length) {
            // 
            console.warn(
              "Formtable "
            );
            ctx.emit("errorEvent", "errorData");
          }
        }
      }
    };
    paramsError(); //
    return {
      SetWidth,
    };
  },
};
</script>

<style lang="less" scoped>
.Formtable {
  border-radius: 5px;
  border-collapse: collapse; /**/
  th,
  td {
    text-align: left;
    padding: 20px 10px;
    box-sizing: border-box;
    border-bottom: 1px solid rgb(231, 231, 231);
  }
  th {
    background: rgb(248, 248, 248);
  }
}
</style>
