<template>
  <div class="PaginationBox">
    <p class="pagebtn iconfont" @click="PageBtn(true)">&#xe602;</p>
    <div class="Paginationitem" v-for="item in Count" :key="item">
      <!--  -->
      <div v-if="Count > 10">
        <!-- actval  Count 66   aroundRange4 -->
        <!--  
                 actval 6 
                    8           64   
                 1 2 3 4 5 6 7      ...65  66

                  item  65 ...
                 -->
        <p v-if="actval < aroundRange + 2">
          <span
            :class="{ act: actval == item, noneber: item == Count - 1 }"
            v-if="item < aroundRange + 4 || item > Count - 2"
            @click="getCurrentPage(item)"
            >{{ item == Count - 1 ? ". . . " : item }}</span
          >
        </p>
        <!--  
                actval 5        actval  62              - aroundRange 4 
                  actval - 4    actval + 4
                 55      actval - 4    actval + 4
                52 53 54   55   56 57 58      
                 64   ...66
                    3    1 ...
                -->
        <p v-if="actval > aroundRange + 1 && actval < Count - aroundRange">
          <span
            :class="{
              act: actval == item,
              noneber: item == Count - 1 || item == 2,
            }"
            v-if="
              (item > actval - aroundRange && item < actval + aroundRange) ||
              item > Count - 2 ||
              item < 3
            "
            @click="getCurrentPage(item)"
            >{{
              item == Count - 1 ? ". . . " : item == 2 ? ". . ." : item
            }}</span
          >
        </p>
        <!--  
                actval  61    - aroundRange+1  5 
                  3        59   
                1 ...(2)  60 61 62 63 64 65 66
                item  2...
                -->
        <p v-if="actval > Count - (aroundRange + 1)">
          <span
            :class="{ act: actval == item, noneber: item == 2 }"
            v-if="item > Count - (aroundRange + 3) || item < 3"
            @click="getCurrentPage(item)"
            >{{ item == 2 ? ". . . " : item }}</span
          >
        </p>
      </div>
      <!--  -->
      <p v-if="Count <= 10">
        <span :class="{ act: actval == item }" @click="getCurrentPage(item)">{{
          item
        }}</span>
      </p>
    </div>
    <p class="pagebtn iconfont" @click="PageBtn(false)">&#xe601;</p>
  </div>
</template>
<script>
import { watch, ref } from "vue";
export default {
  name: "h-pagination",
  props: {
    Count: {
      //  2+2 ##############
      type: Number,
      default: 50,
    },
    aroundRange: {
      //
      type: Number,
      default: 4,
    },
    currentPage: {
      //
      type: Number,
      default: 1,
    },
  },
  setup(props, ctx) {
    const actval = ref(props.currentPage);
    // 
    watch(
      () => props.currentPage,
      () => {
        actval.value = props.currentPage;
      }
    );
    // 
    watch(actval, () => {
      ctx.emit("getCurrentPage", actval.value);
    });
    // 
    const getCurrentPage = function (pageVal) {
      actval.value = pageVal;
    };
    //
    const PageBtn = function (sign) {
      if (sign) {
        //
        if (actval.value !== 1) {
          actval.value--;
        }
      } else {
        //
        if (actval.value !== props.Count) {
          actval.value++;
        }
      }
    };
    return {
      actval,
      PageBtn,
      getCurrentPage,
    };
  },
};
</script>
<style lang="less" scoped>
/*  */
.pagebtn {
  float: left;
  padding: 0 15px;
  -moz-user-select: none; /**/
  -webkit-user-select: none; /*webkit*/
  -ms-user-select: none; /*IE10*/
  -khtml-user-select: none; /**/
  user-select: none;
}
.pagebtn:hover {
  color: rgb(0, 153, 255);
}
/*  */
.Paginationitem span {
  padding: 10px;
  margin: 0 2px;
  -moz-user-select: none; /**/
  -webkit-user-select: none; /*webkit*/
  -ms-user-select: none; /*IE10*/
  -khtml-user-select: none; /**/
  user-select: none;
  border: 1px solid rgb(230, 230, 230);
  border-radius: 5px;
  cursor: pointer;
}
.Paginationitem span.noneber {
  border: none;
}
/*  */
.Paginationitem span:hover {
  background: rgb(211, 211, 211);
}
/*  */
.Paginationitem span.act {
  background: rgb(35, 112, 255);
  color: #fff;
}
.PaginationBox .Paginationitem {
  float: left;
}
.PaginationBox {
  position: relative;
  line-height: 30px;
  float: left;
  left: 50%;
  transform: translate(-50%);
  background: #fff;
}
</style>
