<template>
  <div class="gobang">
    <div class="gobang-title">
      <p class="black-chess-num" :class="{ current: currentPlayChess == 1 }">
         : {{ blackChess }}
      </p>
      <p class="start-btn" @click="initCoordinate"></p>
      <p class="fallback-chess" @click="fallbackChess">
         :  {{ blackLatelyNum }}  {{ whiteLatelyNum }}
      </p>
      <p class="white-chess-num" :class="{ current: currentPlayChess == 2 }">
         : {{ whiteChess }}
      </p>
    </div>
    <div class="formtable-box">
      <p
        class="victory-tips"
        :class="
          victoryTips
            ? victoryTips === ''
              ? 'show black'
              : 'show white'
            : ''
        "
      >
        <span v-for="item in victoryTips" :key="item">{{ item }}</span>
      </p>
      <!--  -->
      <table class="formtable">
        <tr v-for="itema in checkerboardNum" :key="itema">
          <td v-for="itemb in checkerboardNum" :key="itemb"></td>
        </tr>
      </table>
      <!--  -->
      <div class="chess-position-box">
        <p v-for="(item, index) in coordinate" :key="index">
          <span
            class="chess-position"
            :class="judgeChessType(item.pieceType)"
            @click="playChess(index, item.edgeRange)"
          ></span>
        </p>
      </div>
    </div>
    <div class="gobang-bottom">
      <p class="victory-num">{{ blackVictoryNum }}</p>
      <p class="time-num">{{ totalTimeNum }}</p>
      <p class="time-num">{{ bureauTimeNum }}</p>
      <p class="victory-num">{{ whiteVictoryNum }}</p>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, onBeforeUnmount } from 'vue';
import { formatDuring } from '@/utils/common.js';
export default {
  name: '',
  setup() {
    const data = reactive({
      victoryTips: '', //
      blackVictoryNum: 0, //
      whiteVictoryNum: 0, //
      pointNum: 225, //
      blackChess: 113, //
      whiteChess: 112, //
      checkerboardNum: 14, //14*1415*15
      currentPlayChess: 1, //     1    2
      blackLatelyNum: 0, //
      whiteLatelyNum: 0, //
      coordinate: [], //
      totalTimeNum: formatDuring(0), //
      bureauTimeNum: formatDuring(0), //
      totalTimes: null, //
      bureauTimes: null, //
      //   
      boundary: {
        0: 'Ledge',
        14: 'Redge',
        15: 'Ledge',
        29: 'Redge',
        30: 'Ledge',
        44: 'Redge',
        45: 'Ledge',
        59: 'Redge',
        60: 'Ledge',
        74: 'Redge',
        75: 'Ledge',
        89: 'Redge',
        90: 'Ledge',
        104: 'Redge',
        105: 'Ledge',
        119: 'Redge',
        120: 'Ledge',
        134: 'Redge',
        135: 'Ledge',
        149: 'Redge',
        150: 'Ledge',
        164: 'Redge',
        165: 'Ledge',
        179: 'Redge',
        180: 'Ledge',
        194: 'Redge',
        195: 'Ledge',
        209: 'Redge',
        210: 'Ledge',
        224: 'Redge'
      }
    });
    const boundaryArr = []; //key
    const latelyIndex = []; //index
    onMounted(() => {
      initCoordinate(); //
      totalCalculationTime(); //
    });
    // 
    onBeforeUnmount(() => {
      clearInterval(data.totalTimes); //
      clearInterval(data.bureauTimes); //
    });
    // 
    const totalCalculationTime = () => {
      let time = 0;
      clearInterval(data.totalTimes); //
      data.totalTimes = setInterval(() => {
        data.totalTimeNum = formatDuring(time); //
        time += 1000;
      }, 1000);
    };
    // 
    const bureauCalculationTime = () => {
      let time = 0;
      clearInterval(data.bureauTimes); //
      data.bureauTimes = setInterval(() => {
        data.bureauTimeNum = formatDuring(time); //
        time += 1000;
      }, 1000);
    };

    // 
    const fallbackChess = () => {
      if (!latelyIndex.length) return;
      data.coordinate[latelyIndex[latelyIndex.length - 1]].pieceType = 0; //index
      data.currentPlayChess = data.currentPlayChess == 1 ? 2 : 1; //
      data.currentPlayChess == 1
        ? data.blackLatelyNum++
        : data.whiteLatelyNum++;
      data.currentPlayChess == 1 ? data.blackChess++ : data.whiteChess++; //
      latelyIndex.pop(); //
    };
    // 
    const initCoordinate = () => {
      bureauCalculationTime(); //
      data.victoryTips = ''; //
      data.blackChess = 113; //
      data.whiteChess = 112; //
      data.blackLatelyNum = 0; //
      data.whiteLatelyNum = 0; //
      data.coordinate = []; //
      data.currentPlayChess = 1;
      data.latelyIndex = [];
      // key
      for (let key in data.boundary) {
        boundaryArr.push(parseInt(key));
      }
      // xy
      let edgeRange; //
      let num = 0; //
      for (let x = 0; x < data.checkerboardNum + 1; x++) {
        for (let y = 0; y < data.checkerboardNum + 1; y++) {
          // 87
          num == 15 ? (num = 1) : num++; //15
          edgeRange = num <= 8 ? 'Ledge' : 'Redge'; //8Ledge 7Redge
          // 
          data.coordinate.push({
            coor: [x * 50, y * 50], //
            piece: false, //
            pieceType: 0, //
            edgeRange //
          });
        }
      }
    };
    // 
    const playChess = (index, edgeRange) => {
      console.log('', index);
      // 
      const currentType = data.currentPlayChess;
      //  0   0
      if (!data.coordinate[index].pieceType) {
        data.coordinate[index].pieceType = currentType; //
        currentType == 1 ? data.blackChess-- : data.whiteChess--; //
        data.currentPlayChess = currentType == 1 ? 2 : 1; //
        latelyIndex.push(index); //index,
        fiveSubSuccess(index, currentType, edgeRange); //
      } else {
        alert(''); //
      }
    };
    // 5
    const fiveSubSuccess = (index, type, edgeRange) => {
      let num = 0; //
      // 1   15   16  14
      const directionConfig = [1, 15, 16, 14];
      directionConfig.forEach((dir) => {
        // 4
        getIndexList(index, dir, edgeRange).forEach((item) => {
          //++   0
          if (data.coordinate[item].pieceType === type) {
            num++; //++
            if (num === 5) fiveSubComplete(type);
            return;
          } else {
            //0
            num = 0;
          }
        });
      });
    };
    //       4 9
    const getIndexList = (index, num, edgeRange) => {
      let oneNum = index - num * 4; //
      let list = []; //9
      for (let i = 0; i < 9; i++) {
        const currentIndex = oneNum + num * i;
        if (currentIndex >= 0 && currentIndex <= data.pointNum - 1) {
          //1-225  0-224
          list.push(currentIndex);
        }
      }
      // 
      return boundaryHandle(list, index, num, edgeRange);
    };
    // 
    // 
    const boundaryHandle = (list, index, num, edgeRange) => {
      // 
      let myIndex = index;
      // 
      let result = false;
      // 
      if (!data.boundary[myIndex]) {
        // 
        // 
        let mylist = JSON.parse(JSON.stringify(list));
        //  14
        if (num === 14 && edgeRange === 'Ledge') {
          mylist.reverse();
        }
        // 1  16
        if ([1, 16].includes(num) && edgeRange === 'Redge') {
          mylist.reverse();
        }
        // 
        mylist.forEach((item) => {
          if (boundaryArr.includes(item)) {
            myIndex = item; //
            result = true; //
            return;
          }
        });
      } else {
        //   
        result = true;
      }
      // console.log('', myIndex);
      // console.log(list, boundaryArr);

      // 
      if (result) {
        console.log('');
        const listIndex = list.indexOf(myIndex); //
        // 
        // 
        if (data.boundary[myIndex] === 'Ledge') {
          //    
          if (num === 1 || num === 16) {
            list = list.slice(listIndex);
          }
          //    
          if (num === 14) {
            list = list.slice(0, listIndex + 1);
          }
        }
        // 
        if (data.boundary[myIndex] === 'Redge') {
          //    
          if (num === 1 || num === 16) {
            list = list.slice(0, listIndex + 1);
          }
          //    
          if (num === 14) {
            list = list.slice(listIndex);
          }
        }
      }
      // console.log(list);
      return list;
    };

    // 
    const fiveSubComplete = (type) => {
      setTimeout(() => {
        data.victoryTips = type == 1 ? '' : ''; //
        type == 1 ? data.blackVictoryNum++ : data.whiteVictoryNum++;
      }, 100);
    };
    // 
    const judgeChessType = (type) => {
      return type == 1 ? 'black' : type == 2 ? 'white' : '';
    };
    return {
      initCoordinate,
      fallbackChess,
      judgeChessType,
      playChess,
      ...toRefs(data)
    };
  }
};
</script>
<style lang="less" scoped>
