      margin-left: 50px;
      &:hover {
        opacity: 0.8;
      }
    }
    .fallback-chess {
      cursor: pointer;
      background: rgb(47, 47, 47);
      color: #fff;
      font-weight: 700;
      margin-left: 50px;
      &:hover {
        opacity: 0.8;
      }
    }
    .white-chess-num {
      float: right;
    }
  }
  .formtable-box {
    position: relative;
    width: 700px;
    height: 700px;
    margin: 20px;
    .victory-tips {
      position: absolute;
      font-size: 150px;
      height: 750px;
      width: 750px;
      text-align: center;
      z-index: 888;
      font-weight: 700;
      line-height: 700px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
      transition: 1s;
      &.black {
        color: #000;
      }
      &.white {
        color: #fff;
      }
      &.show {
        transform: translate(-50%, -50%) scale(1);
        span {
          height: 0px;
          position: relative;
          display: inline-block;
          margin: 0 3px;
          animation: shake 0.7s ease-in-out infinite;
        }
        span:nth-child(1) {
          animation-delay: 0.1s;
        }
        span:nth-child(2) {
          animation-delay: 0.2s;
        }
        span:nth-child(3) {
          animation-delay: 0.3s;
        }
        span:nth-child(4) {
          animation-delay: 0.4s;
        }

        @keyframes shake {
          0%,
          100% {
            transform: translateY(0px);
          }
          30% {
            transform: translateY(-30px);
          }
          60% {
            transform: translateY(0px);
          }
        }
      }
    }
    .formtable {
      position: absolute;
      background: rgb(242, 200, 154);
      border-collapse: collapse; /**/
      cursor: pointer;
      td {
        text-align: left;
        width: 50px;
        height: 50px;
        box-sizing: border-box;
        border: 1px solid rgb(32, 32, 32);
        box-sizing: border-box;
      }
    }
    .chess-position-box {
      position: absolute;
      width: 750px;
      height: 750px;
      top: -25px;
      left: -25px;
      cursor: pointer;
      .chess-position {
        float: left;
        width: 44px;
        height: 44px;
        margin: 3px;
        border-radius: 50%;
        background: rgba(0, 0, 0, 0);
        &.black {
          background-image: linear-gradient(to right bottom, #000, #000, #bbb);
          box-shadow: 1px 2px 4px 1px #000;
        }
        &.white {
          background-image: linear-gradient(to right bottom, #fff, #fff, #ccc);
          box-shadow: 1px 2px 4px 1px #000;
        }
      }
    }
  }
  .gobang-bottom {
    padding-bottom: 30px;
    .time-num {
      text-align: center;
      font-size: 16px;
      height: 20px;
      line-height: 20px;
      float: left;
      margin-left: 10px;
      &:nth-child(3) {
        margin-left: 80px;
      }
    }
    .victory-num {
      height: 20px;
      line-height: 20px;
      float: left;
      width: 100px;
      font-size: 20px;
      font-weight: 700;
      text-align: center;

      &:nth-child(4) {
        float: right;
      }
    }
  }
}
</style>
