import {  createVNode, render  } from 'vue'
import confirm from './confirm.vue';
// 

 export default (config) => {
    //  
    const { content , title , buttonConfigs ,style} = config
    //  
    if(style?.box) {
      var { top , bgColor ,width ,padding ,borderRadius } = style.box
    }
    if(style?.title) {
      var { titColor,titTextAlign , titLineHeight ,titFontSize ,titFontWeight } = style.title
    }
    if(style?.content) {
      var { conColor,conTextAlign , conLineHeight ,conFontSize ,conFontWeight } = style.content
    }

    //promiseokresolvecloserejectlet
    return new Promise((res, rej) => { 
      // ,
      const temporaryContainer = document.createElement('div')
      // class
      temporaryContainer.setAttribute('class', 'swh_confirm')
      // body
      document.body.appendChild(temporaryContainer)
      // 
      const ok = function () {
        // 
        setTimeout(() => {
          temporaryContainer.remove()
        }, 300);
        res()   //then
      }
      // 
      const close = function () {
        // 
        setTimeout(() => {
          temporaryContainer.remove()
        }, 300);
        rej()   //catch
      }
      // 
      const vnode = createVNode(confirm , 
        { content , 
          title , 
          buttonConfigs ,
          top, bgColor, width, padding, borderRadius,
          titColor, titTextAlign, titLineHeight, titFontSize, titFontWeight,
          conColor, conTextAlign, conLineHeight, conFontSize, conFontWeight,
          ok , 
          close })
      // container
      render(vnode ,temporaryContainer)
    })
}

  
 
